import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import 'lib-flexible/flexible'
import VueResource from 'vue-resource'
import animated from 'animate.css'
Vue.use(VueResource).use(animated)
Vue.prototype.$axios = axios
Vue.config.productionTip = false

//全局前置路由守卫
router.beforeEach((to, from, next) => {
  next(true);


})
import { Tabbar, TabbarItem, Tab, Tabs, Icon, Button, Search, Tag, NavBar, Rate, Slider, Field, Sidebar, SidebarItem, Loading, CellGroup, Cell, Notify, Lazyload ,Toast,Swipe, SwipeItem} from 'vant';

Vue.use(Tabbar).use(TabbarItem).use(Tab).use(Tabs).use(Icon).use(Button).use(Search).use(Tag).use(NavBar)
  .use(Rate).use(Slider).use(Field).use(Sidebar).use(SidebarItem).use(Loading).use(CellGroup).use(Cell).use(Notify).use(Lazyload).use(Toast).use(Swipe).use(SwipeItem);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
